.ebay-analytics-control-bar {
    display: inline-block !important;
    border: 1px solid #9FA2B2;
}

.ebay-analytics-progress-bar {
    border-bottom: 1px solid #2E86AB;
}

.ebay-analytics-progress-bar-indicator {
    background-color: #2E86AB;
    padding: 2px 3px;
    width: 100%;
}

.load-data-button {
    background: transparent;
    border: none;
    margin: none;
    padding: 4px 6px;
    color: white;
    background-color: #2E86AB;
    border-radius: 3px;
}