a,
a:hover,
a:active,
a:visited {
  border: none;
  text-decoration: none !important;
}

body {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}
.fw-500 {
  font-weight: 500;
}

.fw-600 {
  font-weight: 600;
}
.fw-700 {
  font-weight: 700;
}
.product-img {
  width: 150px;
  height: auto;
}

.c-pointer {
  cursor: pointer !important;
}
